import React,{Component} from 'react'
import { withRouter } from "react-router-dom";
import CategoryButtons from "../CategoryButtons/index";
import {Button, Container,Row,Col, Card,CardImg,CardBody,CardTitle,CardSubtitle,CardText} from "reactstrap";
import "./busqueda.css"
class Busquedas extends Component{
    constructor(props) {
        super(props)
    
        this.state = {
             hasProps: false,
             data: this.props.data
        }
    }
    

    componentWillMount() {
        if(this.props.location && this.props.location.state) {
            console.log(this.props.location.state)
            this.setState({ hasProps: true})
        }
    }

    render(){
        return <div>
            <Container className="container-busqueda">
            <CategoryButtons></CategoryButtons>
            </Container>
            <Row className="body-content">
                <Container>
                    <h1>Tu resultado:</h1>
                    <h2>Busca</h2>
                </Container>
            </Row>
              </div>

      
    }
}

export default withRouter(Busquedas);